/* # @mixin font-smoothing()

<https://github.com/css-recipes/font-smoothing>

**This recipe only works for OS X since Windows has a different font rendering method.**

Others sources:

- <http://maximilianhoffmann.com/posts/better-font-rendering-on-osx>
- <https://developer.mozilla.org/en-US/docs/Web/CSS/font-smooth>
- <https://developer.mozilla.org/fr/docs/Web/CSS/font-smooth>
- <http://www.w3.org/TR/WD-font/#font-smooth>

Usage :

Use `$value` to enable or disable font smoothing.

Mixin usage:

~~~
 @include font-smoothing($value);
~~~

Placehoders:

~~~
@extend %font-smoothing--on | %font-smoothing ; // enabled
@extend %font-smoothing--off; // disabled
~~~

Compatibility:  Safari 4+, Firefox 25+ - !Attention  font-smooth is not in spec
Problems in Chrome with webFonts



@param - {true | on | antialiased | grayscale } to enable.
@param - {false | off | subpixel-antialiased | auto } to disable.
@param $font-smooting--helpers - generate helpers class to enable /disable font-smooting

Styleguide libs.mixins.font-smoothing
*/

$font-smooting--helpers: false !default;

@mixin font-smoothing($value: true) {
	$webkit-value: $value;
	$moz-value: $value;
    $standard-value:$value;
		
	@if ($value == true or $value == on or $value == antialiased or $value == grayscale ) {
		$webkit-value: antialiased;
		$moz-value: grayscale;
        $standard-value: always;
	}
	@else if ($value == false or $value == off or $value == "subpixel-antialiased" or $value == auto ) {
		$webkit-value: subpixel-antialiased;
		$moz-value: auto;
        $standard-value: never ;
	}
	
	-webkit-font-smoothing: $webkit-value;
	-moz-osx-font-smoothing: $moz-value;
    font-smooth: $standard-value;
}

%font-smoothing,
%font-smoothing--on {
	@include font-smoothing(true);
}

%font-smoothing--off {
	@include font-smoothing(false);
}

@if ($font-smooting--helpers) {
	.font-smoothing,
	.font-smoothing--on {
		@extend %font-smoothing;
	}
	
	.font-smoothing--off {
		@extend %font-smoothing--off;
	}
}