/** # Config component


Styleguide core.config
*/

@function config($keys, $config: $config){
  $map: $config;
  @each $key in $keys {
        $map: map-get($map,#{$key});
  }
  @if($map == null){
    @error "Parameters : #{$keys} not exist in #{$config}";
  }
  @return $map;
}



@function _boot(){
  $config: ();
  $colors: ('colors': ( ));
  $colors: map-deep-set($config, 'colors', map-merge($base-colors, $action-colors));
  $config: map-extend($config,$config_default, $colors, true);
  //$colors: deep-set($config,'colors',$base-colors);
  //$palette: map-deep-get()
  
  @return $config;
}
