/** # Susy Layout Gala

Une collection de layouts

Any order columns or blocks

http://www.positioniseverything.net/articles/onetruelayout/anyorder

@version - 0.0.1

Stylegiude components.layouts.layoutgala
*/
$blocks: (
  wrapper: '.wrapper',
  content: '.content-main, .content-footer',
  aside: '.content-aside',
  extra: '.content-extra'
) !default;
$container-width: span(8) !default;
$content-width: span(6 of 8) !default;
$aside-width: span(2 of 8) !default;
$extra-width: $aside-width !default;
$gutter: gutter() !default;
// ---------------------------------------------------------------
// @function
// ---------------------------------------------------------------
@function _get-block($selector){
  $block: map-get($blocks,#{$selector});
  @return unquote($block);
}
// ---------------------------------------------------------------
// @mixins
// ---------------------------------------------------------------
@mixin lg-blueprint($color, $selector){
  
  border: 1px solid $color;
  background-color: lighten($color, 40%);
  &:hover {
      &:before {
        content: $selector;
        position: absolute;
        display: block;
        z-index: 10;
        background-color: rgba(black, .8);
        border-radius: 4px;
        color: white;
        padding: .4em;
    }
  } 
}

// -----------------------------------------
// Three percentage colums
// -----------------------------------------
// Layout gala 01
// https://blog.html.it/layoutgala/LayoutGala01.html
@mixin lg-01($content-width,$aside-width,$extra-width,$container-width,$gutter:24){
    
    #{_get-block(wrapper)} {
        float: left;
        width: 100%;
    }
    
   #{_get-block(content)}  {
        margin-left: ($aside-width + $gutter);
        margin-right: ($extra-width + $gutter);
    }
    
    #{_get-block(aside)}  {
        float: left;
        width: $aside-width ;
        margin-left: -100%;
        margin-right: $gutter;
    }
    
    #{_get-block(extra)}  {
        float: left;
        width: $extra-width;
        margin-left: -($extra-width);
    }
}

