// -----------------------------------------
// One clumn liquid and two halves
// -----------------------------------------
// Layout gala 27
// https://blog.html.it/layoutgala/LayoutGala27.html
@mixin lg-27($content-width,$aside-width,$extra-width,$container-width,$gutter:24){
    
    #{_get-block(wrapper)} {
        
    }
    
   #{_get-block(content)}  {
       width: $container-width;
    }
    $split: ($gutter / 2);
    #{_get-block(aside)}  {
        float: left;
        width: (50% - $split) ;
        margin-right: $split;
    }
    
    #{_get-block(extra)}  {
        float: right;
        width: (50% - $split);
        margin-left: $split;
    }
}

