/* # Lists

Styleguide base.lists
*/

/* # ul/ol Lists



.list--default - Reset Default styling for lists
.list--inline

Markup:
<h2>Un-ordered</h2>
<ul class="$modifierClass">
  <li></li>
  <li></li>
  <li></li>
  <li></li>
</ul>
<h2>Ordered</h2>
<ol class="$modifierClass">
  <li></li>
  <li></li>
  <li></li>
  <li></li>
</ol>

@package \base\lists
@see - <https://github.com/thoughtbot/bitters#lists>
@extend %default-ul - Reset ul to default styling
@extend %default-ol - Reset ol to default stiling

Styleguide base.lists.ul
*/
ul,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;

  &%default-ul {
    list-style-type: disc;
    margin-bottom: $base-spacing;
    padding-left: $base-spacing;
  }

  &%default-ol {
    list-style-type: decimal;
    margin-bottom: $base-spacing;
    padding-left: $base-spacing;
  }
}

ul.list--default,
ul.default{
  @extend %default-ul;
}
ol.list--default{
  @extend %default-ol;
}


ul.list--inline,
ul.inline,
ol.list--inline,
ol.inline{
  padding-left: 0;

  li {
    display: inline-block;
    list-style: none;
    padding-left: 1.5em;

    &:first-child {
      padding-left: 0;
    }
  }
}

/* # Definitions lists

.dl--inline - inline definition lists

Markup:
<dl class="$modifierClass">
  <dt></dt>
    <dd></dd>
  <dt></dt>
    <dd></dd>
</dl>

@see - <http://www.the-art-of-web.com/css/format-dl/>
@see - <http://lea.verou.me/2012/02/flexible-multiline-definition-lists-with-2-lines-of-css/>
$bp-dl-inline {sting} ($bp-s !default) - Breakpoint for switchnig between inline or classic definition presentation.;

Styleguide base.lists.dl
*/

dl {
  margin-bottom: $small-spacing;

  dt {
    font-weight: bold;
    margin-top: $small-spacing;
  }

  dd {
    margin: 0;
  }
}

// Flexible multiline definition lists with 2 lines of CSS
// http://lea.verou.me/2012/02/flexible-multiline-definition-lists-with-2-lines-of-css/
dl.dl--inline,
dl.inline {
    dt, dd { display: inline; margin: 0; }
     
    dd:after {
      content: '\A';
      white-space: pre;
    }
}




