/* # @mixin vertical-align

Aligne verticalement des blocks par rapport au conteneur
Parametrer autoprefixer selon le support navigateur requis



@mixin vertical-align
@param $axis (x,y, both !default)
@param $position (absolute, relative !default)
@see - <http://caniuse.com/#feat=transforms2d>
@see - <http://stackoverflow.com/questions/23240114/how-to-vertically-center-a-div-with-sass-compass>
@see - <http://www.sitepoint.com/centering-with-sass/>
@see - <http://webdesignerwall.com/tutorials/css-vertical-centering-everything-need-know>
@see - 7 techniques pour le centrage <http://thenewcode.com/723/Seven-Ways-of-Centering-With-CSS>
@todo - Bug a verifier sur Ipad/iOs


Compatibility IE9+ due to css3 transform

Styleguide tools.mixins.align.vertical-align
*/
@mixin vertical-align($axis: both, $position: relative){
  position: $position;
  @if $axis==y{
      top: 50%;
      transform: translateY(-50%);
  }
  @if $axis==x{
      left: 50%;
      transform: translateX(-50%);
  }
  @if $axis==both{
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
  }
}

