/* # Acessibility navigation

An accessibilty navigation for screen readers

Markup:
<p class="skipnav"><a></a>-<a></a></p>

@see - <http://webaim.org/techniques/skipnav/>

Styleguide components.nav.accessibilitynav
*/

.skipnav {
	text-align: left;
	//display: none;
}
.skipnav a {
	position: absolute;
	left: -10000px;
	width: 1px;
	height: 1px;
	overflow: hidden;
}
.skipnav a:focus,
.skipnav a:active {
	position: static;
	left: 0;
	width: auto;
	height: auto;
	overflow: visible;
	text-decoration: underline;
}