/* # Medias

Markup:
<h2>Image</h2>
<img  alt="Mountain View" width="200" height="200">
<h2>Figure</h2>
<figure>
  <img alt="The Pulpit Rock" width="304" height="228">
</figure>
<h2>Figure + Figcaption</h2>
<figure>
  <img alt="The Pulpit Rock" width="304" height="228">
  <figcaption>Fig1. - A view of the pulpit rock in Norway.</figcaption>
</figure>


@see  - [W3Scholl - html img](http://www.w3schools.com/tags/tag_img.asp)
@see  - [W3School - figcaption](http://www.w3schools.com/tags/tag_figcaption.asp)
@todo - Responsive Figure, Side-by-side Figures : https://gist.github.com/mistergraphx/a46c1f24a74dffa516bc

Styleguide base.medias
*/

img,
picture {
  margin: 0;
  max-width: 100%;
  height: auto;
}

//figure
figure {
  display: table;
  margin: $base-spacing auto;
}

figure > img {
  display: table-cell;
  
  text-align: center;
  vertical-align: middle;
}

figcaption {  
  color: $base-font-color;
  font-style: italic;
  text-align: center;
}

