/** # @function headings

Retourne une liste des selecteurs Hx.

## Usage

```scss
#{headings(1,6)} {
  color: white;
}
```

## Result

```css
h1, h2, h3, h4, h5, h6 {
  color: blue;
}

```


@param $form    {int} (1 !default)   - start iterate
@param $to      {int} (6 !default)  - end iterate
@return                 - list of selectors separated by a colon
@see                    - http://trulycode.com/bytes/sass-function-loop-headings-h1h2h3h4h5h6/

Styleguide toolboxt.functions.typography.headings
*/
@function headings($from: 1, $to: 6) {
    @if $from == $to {
        @return 'h#{$from}';
    } @else {
        @return 'h#{$from},' + headings($from+1, $to);
    }
}
