/* # @mixin iconify

## Usage

~~~
    i {
        @include iconify('quotation-mark-right','before',$margin: 0);
        float:right;
        padding: 0 .5em;
        background-color: $brand-color-primary;
        color: $white;
        line-height:inherit;
    }

~~~


@mixin iconify
@param $icon {string}                       - icon name
@param $position [before !default, after]   - Position of the icon
@param $margin   (5px default)              - Si avant ou après le contenu, il est possible de gérer l'espace
@param $replace {boolean} (false !default)  - Remplace le contenu
@see                                        - Inspiré de <http://jshak.es/easy-icomoon-icons-with-sass/>
$icon-family-name ('icons' !default)        - Nom de l'icon font ex:`font-awesome, icomoon`
$icon-prefix   ('icon' !default)            - Prefix des classes css sans le tiret ex:`icn, icone, icon`
$icon-set {map}                             - icon set map description with name : hex code


Styleguide libs.mixins.iconify
*/

@mixin iconify($icon, $position: "before", $margin: 5px, $replace: false) {
  // on récupère l'icone dans la maps icon-set
  @if variable-exist($icon-set){
    
    @if map-has-key($icon-set, $icon){
      
      // If we're replacing the text, set font-size to 0
      @if $replace {
        font-size: 0;
      }
      // Pseudo-element properties
      &:#{$position} {
          /*@extend .#{$icon-prefix}-#{$icon};*/
          content: map-get($icon-set,$icon);
          
          font-family: #{$icon-family-name};
          speak: none;
          font-style: normal;
          font-weight: normal;
          font-variant: normal;
          text-transform: none;
          line-height: 1;
          
          @if $replace==false{
              @if $position=="before"{
                  margin-right: $margin;
              }
              @if $position=="after"{
                  margin-left: $margin;
              }
          }
          
          @if $replace {
            font-size: 1rem;
          }    
          @content;
      }
    }@else {
        @warn '@mixin Iconify : Impossible de trouver l\'icone '+$icon ;
    }
  
  }@else{
      @error "@mixin iconify : vous devez définir un set d'icone $icon-set";
  }
}