/** # @mixin headings()

## Usage

```scss
@include headings(){
  color: blue;
}
```

@use \libs\function\headings
@param $from    - iteration start   
@param $to      - iteration stop

Styleguide tools.mixins.headings
*/
@mixin headings($from: 1, $to: 6){
    #{headings($from,$to)}{
        @content;
    }
}