/* # notice

Alerts & message box

Refills components : Flash


Markup:
<div class="notice $modifierClass">
<span>This is a $modifierClass message <a href="#">with a link</a></span>
</div>


$prefix  (notice ! default) - class prefix [alert, flash]
$base-spacing (1.5em !default) - 
$alert-color (#fff6bf !default) -
$error-color (#fbe3e4 !default) -
$notice-color (#e5edf8 !default) -
$success-color (#e6efc2 !default) -


Styleguide addons.notice
*/

$notice: (
  prefix: 'notice',
) !default;


$notice-prefix: 'notice' !default;

$base-spacing: 1.5em !default;
$base-border-radius: 4px !default;

$warning-color: #fff6bf !default;
$error-color: #fbe3e4 !default;
$info-color: #e5edf8 !default;
$success-color: #e6efc2 !default;

@mixin notice-layout($spacing:$base-spacing,$radius:$base-border-radius) {
  display: block;
  margin-bottom: $spacing / 2;
  padding: $spacing / 2;
  text-align: center;
  border-radius: $radius;
}

@mixin notice-theme($color) {
  background-color: $color;
  color: darken($color, 60%);
  a {
    color: darken($color, 70%);
    text-decoration: underline;
    &:focus,
    &:hover {
      color: darken($color, 90%);
    }
  }
}

[class^="#{$notice-prefix}"]{
  @include notice-layout();
}

.#{$notice-prefix} {
  @include notice-theme(silver);
}


.#{$notice-prefix}-warning {
  @include notice-theme($warning-color);
}

.#{$notice-prefix}-error {
  @include notice-theme($error-color);
}

.#{$notice-prefix}-info {
  @include notice-theme($info-color);
}

.#{$notice-prefix}-success {
  @include notice-theme($success-color);
}