/* # @mixin contrasted()

Similaire a la fonction de compass contrasted
extrait de beautons

@param $background-color
@param $dark ($dark-text-default !)
@param $light ($light-text-default !)
@function lin2log
@function log2lin
@function brightness
@function color-contrast
@var $contrasted-light-default (white !)
@var $contrasted-dark-default (black !)

Styleguide libs.mixins.contrasted
*/
@import '../functions/math';
@import '../functions/colors';

$contrasted-light-default: white !default;
$contrasted-dark-default: black !default;

@mixin contrasted($background-color, $dark: $contrasted-dark-default, $light: $contrasted-light-default) {
    background-color: $background-color;
    color: color-contrast($background-color, $dark, $light);
}
