/* # Text Helpers

.text--left - align left
.text--center - align center
.text--right - align right
.text--justify - justify

Markup:
<p class="$modifierClass"></p>

$txt-namespace ('txt' !default;)
$txt-separator ('-' !default)
$txt-alignements (left,right,center,justify !default)
%txt-left
%txt-center
%txt-right
%txt-justify

Styleguide helpers.text
*/
$txt-namespace: 'text' !default;
$txt-separator: '--' !default;

$txt-alignements : (left,right,center,justify) !default;



%txt-left{
	text-align: left; 
}
%txt-right{
	text-align: right; 
}
%txt-justify{
	text-align: justify; 
}
%txt-center{
	text-align: center; 
}


@each $align in $txt-alignements {
	.#{$txt-namespace}#{$txt-separator}#{$align} {
		@extend %txt-#{$align};
	}
}
