/* # Float Helpers

1 - block must be contained by a `.clearfix` container 

.float--none - reset float
.float--right - float a block right [1]
.right - shortcut `float--right` [1]
.float--left - float a block left [1]
.left             - shortcut `float--left` [1]
.center           - center a block via margin auto
.clear            - clear both
.pull-right       - float right margin left
.pull-left        - float left margin right

Markup:
<div class="clearfix">
    <p class="$modifierClass"></p>
</div>

@see - <http://concisecss.com/archive/v3.0.0/documentation/core/alignment>  *Removed from latest concise version*
@use - toolbox/mixins/floats extrait de Compass/float/utilities

Styleguide addons.helpers.float
*/
.float--none {
  @include reset-float(); // Display ; Block !default
}

.float--right,
.right {
  @include float(right);
}

.float--left,
.left{
  @include float(left);
}


.pull-right {
  @extend .right;
  margin-left: $small-spacing;
}

.pull-left {
  @extend .left;
  margin-right: $small-spacing;
}

.center {
	margin: 0 auto;
}


%clear {
  clear: both;
}
.clear {
  @extend %clear;
}

hr.clear{
	@extend %clear;
	border-color: transparent !important;
  margin:0; // reset base hr
}

/* # Clearfix

Source : <https://github.com/h5bp/html5-boilerplate>

Clearfix: 


.clearfix - contain floats


For modern browsers

1.  The space content is one way to avoid an Opera bug when the
    `contenteditable` attribute is included anywhere else in the document.
    Otherwise it causes space to appear at the top and bottom of elements
    that receive the `clearfix` class.

2.  The use of `table` rather than `block` is only necessary if using
    `:before` to contain the top-margins of child elements.

Styleguide addons.helpers.clearfix
*/

.clearfix:before,
.clearfix:after {
    content: " "; /* 1 */
    display: table; /* 2 */
}

.clearfix:after {
    clear: both;
}




/* # Centering

You must define the with and height relative position of the parent container

Markup:
<div style="width:100%;height:200px;position:relative;border:1px solid silver;margin: 1em 0;">
  <div class="center--all">
		<button type="button">Click Me!</button>
		<p> Horizontally and vertically centered content.</p>
	</div>
</div>

@see - <https://css-tricks.com/centering-in-the-unknown/>
@see - <http://concisecss.com/documentation/core/alignment>
$center--all - placeholder

Styleguide addons.helpers.centerall
*/
%center--all {
  left: 50%;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
}
.center--all {
  @extend %center--all;
}

/* # full-screen

Utilise le unitée vh et vw (viewport width et viewport height)

@see  - https://github.com/KingPixil/wing/blob/master/src/util.css

Compatibility: untested

Styleguide helpers.fullscreen
*/

.full-screen {
  width: 100vw;
  height: 100vh;
}

/* # Media Fluid


Markup:
<img class="media--fluid"/>

Styleguide helpers.media--fluid
*/
.media--fluid {
  max-width: 100%;
  height: auto;
}

/* images style effects



%img-rounded - placeholder
%img-circle
%img-bordered

Styleguide addons.helpers.images
*/
/* images style effects */
%img-rounded{
	border-radius: $base-border-radius;
}
.img-rounded{
	@extend %img-rounded;
}
%img-circle {
	border-radius: 50%;
}
.img-circle{
	@extend %img-circle;
}
%img-bordered {
	padding: 4px;
	background-color: #fff;
	border: 1px solid #bbb;
}
.img-bordered {
	@extend %img-bordered;
}