/** # Unit Tools

Styleguide tools.functions.units
*/

/* # @function em2px

Renvoie la valeur en px d'une valeur en em
en multipliant par la taille par default $em-base
supprime l'unite, on peut aussi utiliser fragments/convert()

@param number
@param $em-base {int} (16 !default) - em base
@use - Bourbon strip-unit

styleguide tools.functions.units.em2px
*/
@function em2px($number, $em-base:16){
    @return strip-units($number) * strip-units($em-base);
}

/* # @function convert

##Usage :

```
// PIXELS
// px2em
font-size: convert(18, px, ems);
// px2%
font-size: convert(18, px, percent);
// px2points
font-size: convert(16,px,pts);
// POINTS
// pt2ems
font-size: convert(13, pt, ems);
// pt2px
font-size: convert(13, pt, px);
// pt2%
font-size: convert(13, pt, percent);
// EMs
// em2px
font-size: convert(2.5, ems, px);
//em2%
font-size: convert(2.5, ems, percent);
// %
// %2px
font-size: convert(234, percent, px);
```

@param $value {int} - The mesure you want to convert
@param $currentUnit {string} (px,ems,percent,pt) - From
@param $convertUnit {string} (px,ems,percent,pt) - Destination
@param $em-base (16px !defaut) - Bourbon em base
@var $point-per-inch (72 !default) - Default value for pt conversions
@var $pixel-per-inch (96 (default) - Default value for pt conversions
@see -  Unit and convertions rules <http://outils-css.aliasdmc.fr/convertir-unites-de-longueurs-css.php#note_2>
@see - <http://www.sitepoint.com/understanding-sass-units/>
@source - <http://www.sitepoint.com/converting-typographic-units-sass/>

Styleguide tools.functions.units.convert
*/

// Default value for pt convertions 
$point-per-inch:72 !default; 
$pixel-per-inch:96 !default;

// Bourbon Neat default 
$em-base: 16px !default;

@function convert($value, $currentUnit, $convertUnit,$em-base:$em-base){
  // First we get the $em-base defined in Bourbon Neat
  // or set it explicitly when the function is called
  @if unitless($em-base){
      $error: "#{$em-base} has no unit, you must set the unit (px) explicitly to convert";
      @warn $error;
      @return $error;
  }
  // px $em-base
  @else if unit($em-base) != 'px'{
      $error: "#{$em-base} must be in (px) to convert";
      @warn $error;
      @return $error;
  }
  @else {
    $em-base: strip-units($em-base);
  }
  
  
  
  @if $currentUnit == px{
    
    @if $convertUnit == ems{
      @return ($value / $em-base)+em;
    }
    @else if $convertUnit == percent{
      @return percentage($value / $em-base);
    }
    @else if $convertUnit == pts {
      // taille en pixels * (points par pouces / pixels par pouces) 
      @return $value * ($point-per-inch / $pixel-per-inch)+pt;
    }
  
  }@else if $currentUnit == ems{
   
    @if $convertUnit == px{
      @return $value * $em-base+px;
    }
    @else if $convertUnit == percent{
       @return percentage($value);
    }
  
  }@else if $currentUnit == percent{
  
    @if $convertUnit == px{
      @return $value * $em-base / 100+px;
    }
    @else if $convertUnit == ems{
      @return $value / 100+em;
    }
 
  }@else if $currentUnit == pt{
    $pts-ratio: $em-base * ($point-per-inch / $pixel-per-inch);
    @if $convertUnit == px{
      // convert point to pixel: pixels = point * 96 (default pixel per inch) / 72 (default point per inch)
      @return $value * ($pixel-per-inch/$point-per-inch)+px;
    }
    @else if $convertUnit == ems{
      @return $value / $pts-ratio +em;
    }
    @else if $convertUnit == percent{
        @return percentage($value / $pts-ratio);
    }
  }
}


