/* # Buttons

## Css output

```
  button,
  [type='button'],
  [type='reset'],
  [type='submit'] 
```

Markup:
<button type="button">Click Me!</button><br>
<input type="button" value="Input type - button"><br>
<input type="reset" value="Input type - reset"><br>
<input type="submit" value="Input type - submit"><br>

@todo [1] - ?? vf normalize
$all-button-input - $all-button - Boubon predefined shortcut
@see  - <http://bourbon.io/docs/#complete-list> Html5 INPUTS

Styleguide base.buttons
*/
$base-btn-border: $base-border !default;
$base-btn-color: rgba(#fff, 1) !default;
$base-btn-foreground: color-contrast($action-color) !default;
$base-btn-background: $action-color !default;

#{$all-buttons}{
  display: inline-block;
  cursor: pointer;
  
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  @include prefixer(appearance, none, ("webkit", "moz"));
  @include font-smoothing(true);
  
  background-color: $base-btn-background;
  border: $base-btn-border;
  border-radius: $base-border-radius;
  
  color: $base-btn-color;
  
  font-family: $base-font-family;
  font-size: $base-font-size;
  font-weight: 600;
  line-height: $base-line-height;
  padding: $small-spacing $base-spacing;
  
  text-align: center;
  text-decoration: none;
  
  &:hover,
  &:focus {
    background-color: shade($base-btn-background, 25%);
    color: contrasted($base-btn-background);
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;

    &:hover {
      background-color: $action-color;
    }
  }
}
