/** # Color Component


$_colors global() -   
@mixin color-swatch($colors) -

Styleguide core.color
*/
@if not global-variable-exists(_colors) {
  $_colors: () !global;
}


/** # @function color

renvoie une couleur depuis la config.colors d'apres un keyword color-name,
la couleur peut etre une map des teintes, dans ce cas si aucune teinte n'est specifie c'est la teinte base qui est retournee


@function color() - renvoie une couleur depuis la config.colors d'apres un keyword color-name
@param $color {color|map}
@param $tone {key} (base !default)
@return {color}

Styleguide core.colors.color
*/
@function color($color, $tone: 'base'){
  $req: (colors);
  @each $key in $color {
      $req: append($req, $key, comma);
  }
  $color: config($req);
  @if(is-map($color)){
    $color: map-get($color,$tone);
  }
  @if(is-color($color)){
      @return $color;
  }
  @error "#{color} is not a color, or a map";
}
// BUILDING COLOR PALETTE WITH SASS :
// http://codepen.io/SitePoint/pen/lqLvD?editors=0100
// https://www.sitepoint.com/using-sass-build-color-palettes/
// Markup
//<ul class="color-swatche blue">
//	<li></li>
//	<li></li>
//	<li></li>
//	<li></li>
//</ul>
// Usage
// @include color-swatch($yellow-shades);
@mixin color-swatch($colors){
  $i: 1;
  @each $name,$value in $colors {
    //$name: $value;
    
    &:nth-child(#{$i}){
      background-color: unquote($value);
      &::after {
        content: '#{$i} #{$name} + #{$value}';
      }
    }
    
    //value: $i;
    $i: $i+1;
  }
}