
/* # Visibility helpers

<https://github.com/h5bp/html5-boilerplate>

.invisible              - Hide visually and from screen readers, but maintain layout
.hidden                 - Hide visually and from screen readers: h5bp.com/u
.visuallyhidden         - Hide only visually, but have it available for screen readers: h5bp.com/v
.sr-only				- Visible just for screen readers
%hidden 				- hidden placehoder
@mixin visuallyhidden 	- Visuallyhidden
@mixin sr-only			- screen reader

Styleguide addons.helpers.visibility
*/

/*
 * Hide visually and from screen readers: h5bp.com/u
 *
 */
%hidden{
    display: none !important;
    visibility: hidden;
}
.hidden {
  @extend %hidden;
}


.visuallyhidden{
    @include visuallyhidden;
}

// Extends the .visuallyhidden class to allow the element to be focusable
// when navigated to via the keyboard: h5bp.com/p
.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

// Hide visually and from screen readers, but maintain layout
.invisible {
    visibility: hidden;
}


// Screen reader only
@mixin sr-only(){
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only {
   @include sr-only();
}
