/* Color Helpers

Definir des classes utilitaires pour les couleures
https://shakrmedia.github.io/petal/docs/colors.html propose des classes txt-[color]-[l|d]
on definie via le suffixe une intensite light ou dark suivi d'une valeur numerique
on suis le meme principe pour la couleur de background avec .bg-color-densite

@todo - relativement mal nommé on est en conflit avec le composant du core color

Styleguide helpers.colors
