/* # Icons

Accessibility issues :
<https://www.filamentgroup.com/lab/bulletproof_icon_fonts.html>

Performance issues:
<http://calendar.perfplanet.com/2016/no-font-face-bulletproof-syntax/>

Une version du markup amelioree, et accessible
<https://css-tricks.com/html-for-icon-font-usage/>

Automatiser la preparation des formats a servir aux differents peripheriques
<https://www.filamentgroup.com/lab/grunticon.html>

https://css-tricks.com/svg-sprites-use-better-icon-fonts/

IconMoon
<https://icomoon.io/#docs>

Markup:
<p><span class="icon-tag" aria-hidden="true"></span></p>

@see - Creer <http://fontello.com/>
@see - Automatiser <https://www.npmjs.com/package/gulp-fontello>
@see - Utilitaire <http://getthemap.mister-graphx.com/>
$icon-family-name {string} (icons !default)
$icon-prefix {string} (icon !default)
$icon-set {map} (name:hex code) -
$file-format - 
@use Bourbon/font-face


Styleguide addons.icons
*/
$icon-font-path: "../fonts/icons/font/" !default;
$icon-family-name: 'icons' !default;
$icon-prefix: 'icon' !default;
$icon-set: () !default;

// Import WebFont with Bourbon
// http://bourbon.io/docs/#font-face
@include font-face(#{$icon-family-name}, '#{$icon-font-path}#{$icon-family-name}', $file-formats: eot woff2 woff ttf svg);

[class^="#{$icon-prefix}-"]:before,
[class*="#{$icon-prefix}-"]:before {
  font-family: "#{$icon-family-name}";
  font-style: normal;
  font-weight: normal;
  speak: none;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
     
  /* fix buttons height, for twitter bootstrap */
  line-height: 0em;
 
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
 
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
 
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
  
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale;
}

/* # @mixin icon-set-init()


@param - $icon-set {map} (null !)
Styleguide addons.icons.mixins
*/
@mixin icon-set-init($icon-set-map: null){
  @if $icon-set-map{
    @each $name, $code in $icon-set-map{
      .#{$icon-prefix}-#{$name}:before{
        content:$code;
      }
    }
  }
  @else{
    @error "Vous n'avez pas fourni de icon-map";
  }
}

@include icon-set-init($icon-set);