///////////////////////////////////////////////
/// SETTINGS !default
//////////////////////////////////////////////
// Modernizer
// https://modernizr.com/docs/#using-modernizr-with-css
$modernize: false !default;


// ------------------------------------------------*/
// GLOBAL
// Shared globaly between libs
// ------------------------------------------------*/

$global-box-sizing: border-box !default;


// ------------------------------------------------*/
// Bourbon !globals
// http://bourbon.io/docs/#settings
// ------------------------------------------------*/

//$bourbon: (
//	"contrast-switch-dark-color": #000,
//    "contrast-switch-light-color": #fff,
//    "global-font-file-formats": ("ttf", "woff2", "woff"),
//    "modular-scale-base": 1em,
//    "modular-scale-ratio": $major-third,
//    "rails-asset-pipeline": false,
//);

// ------------------------------------------------*/
// Colors
// 
// ------------------------------------------------*/
$base-blue: #477dca !default;
$palette: (
  'yellow': (
    'light': #fff200,
    'base': #ffcb04,
    'medium': #F9B30E,
    'dark': #F7B602,
  ),
  'blue': (
    'light': tint($base-blue, 40%),
    'base': $base-blue,
    'medium': shade($base-blue, 20%),
    'dark': shade($base-blue, 40%),
  ),
  'orange': '#FFBA47',
  'green': '#FF5B44'
);

// Colors variables
$white: #fffefc !default;
$black: #080501 !default;

$red: #c4151c !default;
$light-red: #FBE3E4 !default;

$yellow: #ffcb04 !default;
$light-yellow: #fff200 !default;

$dark-purple: #460045 !default;
$medium-purple: #7A035D !default;
$light-purple: #7A035D !default;

$pink: #fc0084 !default;
$dark-pink: #C60E90 !default;
$medium-pink: #FFC1FF !default;
$light-pink: #FFC1FF !default;

$green: #63ab22 !default;
$dark-green: #283b19 !default;
$medium-green: #385c17 !default;
$light-green: #87b551 !default;

$blue: #477dca !default;
$light-blue: #93BEF9 !default;
$medium-blue: #144487 !default;
$dark-blue: #0B1F3D !default;

$gray : #808080 !default;
$dark-gray: #333 !default;
$medium-gray: #999 !default;
$light-gray: #ddd !default;

// ------------------------------------------------*/
// BRAND
// ------------------------------------------------*/

$brand-color-primary: #3694b8 !default;
$brand-color-secondary: #e37799 !default;
$brand-color-tertiary: #ba9d75 !default;

// ------------------------------------------------*/
// Base
// ------------------------------------------------*/


$base-unit: em !default;

// @media print
// @file - base.print
// If we use rem/em printers must have a font-size setting in pt
// @see - https://github.com/jptaranto/typey/blob/master/stylesheets/typey/mixins/_define-type-sizing.scss
$print-font-size: 12pt !default; 

// Typography
// To set your em-base & base-line-height you can use :
// Golden ratio calculator <http://www.pearsonified.com/typography/>

// Set base

// Base line-height
// With a unit to work with suzy : https://github.com/oddbird/susy/issues/484
//
$em-base: 16px !default;
$base-line-height: em(26px) !default;

$base-font-family: $font-stack-helvetica !default;
$base-font-size: 1em !default;
$base-background-color: #fff !default;
$base-font-color: $dark-gray !default;

// Body
$body-background: $white !default;
$body-font-color: $base-font-color !default;
$body-antialiased: false !default // Enable web-kit-font-smoothing

// headings
$heading-font-family: $base-font-family !default;
$heading-line-height: 1.2 !default;

// Spacing
$base-spacing: $base-line-height !default;
$small-spacing: ($base-line-height / 2) !default;

// Z-index
$base-z-index: 0 !default;

// Action Colors
$action-color: $green !default;

// Selection
$base-selection-bg-color: $light-gray !default;
$base-selection-color: color-contrast($base-selection-color) !default;

// Borders
$base-border-color: $light-gray !default;
$base-border: 1px solid $base-border-color !default;
$base-border-radius: 3px !default;

// base.buttons
$base-btn-border: $base-border !default;
$base-btn-color: rgba(#fff, 1) !default;
$base-btn-foreground: $base-btn-color !default;
$base-btn-background: $action-color !default;

// Forms
$form-box-shadow: inset 0 1px 3px rgba(#000, 0.06) !default;
$form-box-shadow-focus: $form-box-shadow, 0 0 5px adjust-color($action-color, $lightness: -5%, $alpha: -0.3) !default;


// ------------------------------------------------*/
// LAYOUT & GRID
// ------------------------------------------------*/

$max-width: em(960) !default;

// ------------------------------------------------*/
// Set the grid :
// http://gridulator.com/
// http://grid.guide/
// http://susydocs.oddbird.net/en/latest/
// ------------------------------------------------*/

$susy: (
  flow: ltr,
  math: fluid,
  output: float,
  gutter-position: after,
  container: auto,
  container-position: center,
  columns: 8,
  gutters: .25,
  column-width: false,
  global-box-sizing: $global-box-sizing,
  last-flow: to,
  debug: (
    image: hide,
    color: rgba(#66f, .25),
    output: background,
    toggle: top right,
  ),
  use-custom: (
    background-image: true,
    background-options: false,
    box-sizing: true,
    clearfix: false,
    rem: true,
  )
) !default;

// ------------------------------------------------*/
// Breakpoints
// http://include-media.com/
// ------------------------------------------------*/
// http://include-media.com/
// http://thesassway.com/intermediate/responsive-web-design-in-sass-using-media-queries-in-sass-32
// https://css-tricks.com/approaches-media-queries-sass/


// ------------------------------------------------*/
// Colors
// ------------------------------------------------*/

$brand-colors: ();
$action-colors: (
  'success': green,
  'info': blue,
  'notice': silver,
  'warning': orange,
  'danger': red
);
$base-yellow: #fdd808;
$base-blue: #3d67d3;
$base_colors: (
  'yellow': (
    'light': tint($base-yellow, 40%),
    'base': $base-yellow,
    'medium': #fdd808,
    'dark': #fdd808,
  ),
  'blue': (
    'light': tint($base-blue, 40%),
    'base': $base-blue,
    'medium': shade($base-blue, 20%),
    'dark': shade($base-blue, 40%),
  ),
  'orange': ('base': #FFBA47),
  'green': ('base': #FF5B44)
);

$config_default: (
  'layout': (
    'small': 320px
  ),
  'colors': (
    
  )
);