/* # shadows

[Spec](http://www.w3schools.com/cssref/css3_pr_text-shadow.asp)

@micin single-text-shadow - set a text-shadow with params or defaults
@see - https://github.com/matthieua/sass-css3-mixins/blob/master/css3-mixins.scss
@see - http://compass-style.org/examples/compass/css3/text_shadow/
@see - http://compass-style.org/reference/compass/css3/text-shadow/
@see - https://css-tricks.com/snippets/css/css-text-shadow/


Styleguide libs.mixins.shadows
*/

/* # @mixin single-text-shadow


@param $x {int}  (2px !) - X
@param $y {int} (2px !) - Y
@param $blur {int} (2px !) - Blur
@param $color {Color} (rgba(0,0,0,.4) !) - Color

Styleguide libs.mixins.shadows.single-text-shadow
*/
@mixin single-text-shadow($x: 2px, $y: 2px, $blur: 5px, $color: rgba(0,0,0,.4)) {
    text-shadow: $x $y $blur $color;
}
/* # @mixin single-box-shaow


@param $x {int}  (2px !) - X
@param $y {int} (2px !) - Y
@param $blur {int} (2px !) - Blur
@param $color {Color} (rgba(0,0,0,.4) !) - Color

Styleguide libs.mixins.shadows.single-box-shadow
*/
@mixin single-box-shadow($x: 2px, $y: 2px, $blur: 5px, $color: rgba(0,0,0,.4)) {
    box-shadow: $x $y $blur $color;
}
