/* # @function pow()

Positive or negative integer exponents
https://css-tricks.com/snippets/sass/power-function/

$base -
$exp - exposant

Styleguide tools.functions.math.pow
*/
@function pow($base, $exp) {
  $value: $base;
  @if $exp > 1 {
    @for $i from 2 through $exp {
      $value: $value * $base;
    }
  }
  @if $exp < 1{
    @for $i from 0 through -$exp {
      $value: $value / $base;
    }
  }
  @return $value;
}
/* # @function fact()



$num -
@return - factorial of the number

Styleguide tools.functions.math.fact
*/
@function fact($num) {
  $fact: 1;
  @if $num > 0{
    @for $i from 1 through $num {
      $fact: $fact * $i;
    }
  }
  @return $fact;
}



/* # @function lin2log()

LINEAR to LOGARITHMIC and vice-versa
https://fr.wikipedia.org/wiki/%C3%89chelle_logarithmique

@use - pow()

Styleguide tools.functions.math.lin2log
*/
@function lin2log($n) {
    @if $n <= 0.0031308 {
        @return $n * 12.92;
    } @else {
        @return 1.055 * pow($n,1/2.4) - 0.055;
    }
}
@function log2lin($n) {
    @if $n <= 0.04045 {
        @return $n / 12.92;
    } @else {
        @return pow((($n + 0.055)/1.055),2.4);
    }
}



/* # Trigo

Trigonometrie :
https://fr.wikipedia.org/wiki/Trigonom%C3%A9trie
- sin(), cos(), tan()
Grades : https://fr.wikipedia.org/wiki/Grade_(angle)
Radian : radian (symbole : rad) est l'unitee derivee du Systeme international qui mesure les angles plans. 
https://fr.wikipedia.org/wiki/Radian

Other tools & inspirations:
- https://github.com/at-import/Sassy-math
- https://css-tricks.com/snippets/sass/power-function/

@see - http://japborst.net/posts/sass-sines-and-cosines
@see - http://codepen.io/NyX/pen/dYvymM 

Styleguide tools.function.math.trigo
*/

$pi: 3.14159265359;
$_precision: 10;


@function _to_unitless_rad($angle) {
  @if unit($angle) == "deg" {
    $angle: $angle / 180deg * $pi;
  }
  @if unit($angle) == "rad" {
    $angle: $angle / 1rad;
  }
  @return $angle;
}
/* # @function sin()

Calcule le sinus d'un angle
https://fr.wikipedia.org/wiki/Sinus_(math%C3%A9matiques)

$angle  - angle en deg
@use    - _to_unitless_rad()

Styleguide tools.functions.math.sin
*/
@function sin($angle){
  $a: _to_unitless_rad($angle);
  $sin: $a;
  @for $n from 1 through $_precision {
    $sin: $sin + (pow(-1, $n) / fact(2 * $n + 1) ) * pow($a, (2 * $n + 1));
  }
  @return $sin;
}
/* # @function cos()

Calcule le cosinus d'un angle
https://fr.wikipedia.org/wiki/Sinus_(math%C3%A9matiques)

$angle  - angle en deg
@use    - _to_unitless_rad()

Styleguide tools.functions.math.cos
*/
@function cos($angle){
  $a: _to_unitless_rad($angle);
  $cos: 1;
  @for $n from 1 through $_precision {
    $cos: $cos + ( pow(-1,$n) / fact(2*$n) ) * pow($a,2*$n);
  }
  @return $cos;
}
/* # @function tan()

Calcule la tangeante d'un angle
https://fr.wikipedia.org/wiki/Sinus_(math%C3%A9matiques)

$angle  - angle en deg
@use    - sin(), cos()

Styleguide tools.functions.math.tan
*/
@function tan($angle){
  @return sin($angle) / cos($angle);
}