$sans-serif: 'Bebas', $sans-serif;
$base-transition: all 0.1s linear !default;
$rm-drop-sub-width: 200px !default;


.responsive-menu {
	background-color: $base-background-color;
	color: $base-font-color;
	@include padding($small-spacing);
	
	.mobile-button {
		@include iconify('menu');
		font-size: 1.5rem;
		color: $dark-gray;
		
		&.menu-opened {
			@include iconify('close');
			background-color: $white;
			color:$black;
			padding:0;
		}
	}  
  
	// Styles du panneau menu ouvert en mode mobile
	& .menu-container > ul {
	  background-color: $white;
	  //border-right: 1px solid $black;
	  font-family: $sans-serif;
	  font-weight: 400;
	  text-transform: uppercase;
	  
	  @include media('>tablet'){	  
		  border: none transparent;
		  background-color: transparent;
		  text-align: left;
	  }
	}
}


@include media('>tablet'){
    .responsive-menu.sticky {
        
    }
}


// On ne style pas en mode large/desktop
@include media('<=tablet'){
  .menu-container.is-open {
		// Theme du fond de page cliquable
		background-color: rgba($base-background-color, .85);
  }
}


// First level
.menu-container li > a {
	// styles touch/mobile
	color: $base-background-color;
	font-family: $sans-serif;
	font-weight: bold;
	font-size: 1rem;
	border-left: 5px transparent solid;

	@include media('>tablet'){
		color: $white;
		border-left: 0 transparent;
		border-bottom: 5px $base-font-color solid;
	}
}

.menu-container li:focus > a,
.menu-container li:hover > a,
.menu-container li > a:hover,
.menu-container li > a:focus
.menu-container li.on a,
.menu-container li.active a{
	background-color: rgba($light-gray, .2);
	border-left: 5px $black solid;
	
	@include media('>tablet'){
		border-left: none transparent;
		border-bottom: 5px rgba($base-font-color, .5) solid;
	}
} 


// Test pour la navigation clavier
.has-sub > a:focus > ul{
	border-color: red;
}

	
.menu-container .has-sub {
	& > ul {
		// transition: opacity .25s linear;
		// defini dans responsive_menu
	}
	& > ul li a {
		font-size: .8em;
		
		@include media('>tablet'){
			background-color: $base-background-color;
			border: none;
			width: 100%;
			display: block;
			&:hover {
				border: none;
			}
		}
		
	}
}

// Bouton signalant l'arbo du menu avec etat deplie/replie
.submenu-button {
	&:after{
		//content:'+';
		//font-size:1rem;		
	}
	&.submenu-opened {
		&:after {
			//content: '-';
		}
	}
}


