/* # Color Tools

https://www.sitepoint.com/using-sass-build-color-palettes/


Styleguide tools.functions.colors
*/
$dark-text-default: $black !default;
$light-text-default: $white !default;
/* # @function brightness()


Styleguide tools.functions.colors.brightness
*/
@function brightness($color) {
  @if type-of($color) == color {
    @return (red($color) * 0.299 + green($color) * 0.587 + blue($color) * 0.114) / 255 * 100%;
  }
  @else {
    @return unquote("brightness(#{$color})");
  }
}

/* # @function color-contrast

Compares contrast of a given color to the light/dark arguments and returns whichever is most "contrasty"

Styleguide tools.functions.colors.colorcontrast
 */
@function color-contrast($color, $dark: $dark-text-default, $light: $light-text-default) {
  @if $color == null {
    @return null;
  }
  @else {
    $color-brightness: brightness($color);  
    $light-text-brightness: brightness($light);
    $dark-text-brightness: brightness($dark);
    
    @return if(abs($color-brightness - $light-text-brightness) > abs($color-brightness - $dark-text-brightness), $light, $dark);  
  }
}
