$sans-serif: 'Bebas', $sans-serif !default;
$base-transition: all 0.1s linear !default;
$base-background-color: $white !default;
$rm-drop-sub-width: 200px !default;
$rm-menu-mobile-width: 250px !default;
$rm-base-z-index: 1000 !default;

// pour les screen-reader on evite display:none
@mixin hide-item(){
	max-height: 0;
	opacity: 0;
	left:-5000px;
}

@mixin show-item(){
	max-height: 100%;
	opacity: 1;
	left:0;
}



// LAYOUT
.responsive-menu {
	position:relative;
	z-index: $rm-base-z-index; // on intialize le z-index réutilisé plus loin
	.head-desktop {
		display:none;
	}
	.head-mobile {
		
	}
	@include media('>tablet'){
		@include full;

		.head-desktop {
			display:block;	
		}
		.head-mobile {
			display:none;
		}
		.menu-container > ul {			
			
		}
	}
}



.responsive-menu {
	.mobile-button {
		position: relative;
		float: left;
		z-index: $rm-base-z-index + 10;
		cursor: pointer;
		padding-right: $small-spacing;

		font-size: 1.5rem; // Taille de l'icone
		transition: transform .2s linear ;
		&.menu-opened {
			z-index: $rm-base-z-index + 150;
			position: fixed;
			top: 0;
			// left: -10px; // Correction du padding du bloc parrent
			
			transform: translateX($rm-menu-mobile-width - (strip-unit($base-spacing) / 2));
		}
	}  
  
  // Animation et positionnement du menu mobile ouvert
  // $rm-menu-mobile-width: 350px !default;
  // positionnement = largeur du menu - padding;
  & .menu-container > ul {
      display:block;
      position: absolute;
      width: $rm-menu-mobile-width;
      height: 100vh;
			overflow-y: scroll; // Ici si on en déclare un il faut initialiser les deux axes
			overflow-x: hidden;
      top: 0;
      //left: -($rm-menu-mobile-width - (strip-unit($base-spacing) / 2));
			left: -5000px;

      z-index: 100;
      padding: $base-spacing;
            
      transition: transform .2s linear ;
			
      &.is-open{
         transform: translateX(5000px);
			}
      
		@include media('>tablet'){
			position: relative;
			top: 0;
			left: 0;
			width:initial; // 
			height: initial;
			overflow-y: visible; // Ici si on en déclare un il faut initialiser les deux axes
			overflow-x: visible;
			display: flex;
			justify-content: flex-end;
			align-items: flex-end;
			padding: 0;
		}
  }
  // Style des menu>items
  & .menu-container > li {
    @include media('>tablet'){
			display: inline-block;
			//text-align: right;
    }
  }

}// ./responsive-menu



// 

// On ne style pas en mode large/desktop
@include media('<=tablet'){
  .menu-container.is-open {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top:0;
		left: 0;
    z-index: $rm-base-z-index + 60;
		// Theme
		//background-color: rgba($black, .85);
  }
}


// First level
.menu-container li > a {
	display: block;
	white-space: nowrap;
	padding:  $small-spacing;
	
	transition: background .3s linear;

	@include media('>tablet'){
			display: flex;
	}
}
.menu-container li:focus > a,
.menu-container li:hover > a,
.menu-container li > a:hover,
.menu-container li > a:focus{

	
	@include media('>tablet'){

	}
} 

// LAYOUT des sous-menus

.has-sub,
.menu-container ul li { //mieux cibler car la class has-sub n'est pas encore la au chargement
	position: relative; // Assurer le positionnement des sousmenus
	
	& > ul {
		@include hide-item;
		
		&.open{
			@include show-item;
		}
		
		@include media('>tablet'){
			position: absolute;
			width: $rm-drop-sub-width;
		}
	}
	
	& > ul ul {
		@include hide-item;
		
		&.open{
			@include show-item;
		}
		
		@include media('>tablet'){
			//position: absolute;
			//width: $rm-drop-sub-width;
			top:0;
			margin-left: 100%;
		}
	}
}

.has-sub > a:focus > ul{
	//border-color: red;
}

.has-sub:focus > ul,
.has-sub:hover > ul {
	@include media('>tablet'){
			@include show-item;
	}
}
	
.menu-container .has-sub {
	& > ul {
		transition: opacity .25s linear;
	}
	& > ul li a {
			width: 100%;
			display: block; // remplir l'espace disponible pour le placement du submenu button
	}
}

// Bouton signalant l'arbo du menu avec etat deplie/replie
.submenu-button {
	display: block;
	float: right;
	width: 16px;
	height: 16px;
	margin-left: $small-spacing;

	&:after{
		content:'+';
		font-size:1rem;		
	}
	&.submenu-opened {
		&:after {
			content: '-';
		}
	}
}


