/* # Body 

Initialize the base line height and font size for document html/body

Styleguide base.body
*/

html {
  box-sizing: $global-box-sizing;
  font-size: $base-font-size;
}

// Set box-sizing globally to handle padding and border widths
*,
*::before,
*::after {
  box-sizing: inherit;
}


body {
  font-feature-settings: "kern", "liga", "pnum"; // Autoprefixer
  
  margin: 0;
  padding: 0;
  
  background: $body-background;
  color: $body-font-color;
  
  font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: strip-unit($base-line-height);
  
  @if ($body-antialiased) {
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
  }
}