/* # @function makelongshadow

usage:
```
	 text-shadow: makelongshadow(darken($color-twitter, 10%));
```

@link - http://codepen.io/CreativePunch/pen/sIcoB
@param $color - 

Styleguide tools.functions.shadows.makelongshadows
*/
@function makelongshadow($color) {
  $val: 0px 0px $color;
  @for $i from 1 through 35 {
    $val: #{$val}, #{$i}px #{$i}px #{$color};
  }
  @return $val;
}