/* # Badges

Refills components : badges

Voir et s'inspirer de Semantic UiKit pour le composant Badge…

Une classe `.circle` pour des badges en pastille

Markup:
<div>
  <span class="badge">4</span>
  <span class="badge-alert">4</span>
  <span class="badge-error">4</span>
  <span class="badge-notice">4</span>
  <span class="badge-success">4</span>
</div>

@version - v1.0.1
@mixin badge() - 
@mixin badge-theme($color) {color} ($badge-default-color !default) - 
$badge-font-size - set the badge font-size, the border radius is proportional * 5

Styleguide addons.uikit.badges
*/

$medium-gray: #999 !default;
$alert-color: #fff6bf !default;
$error-color: #fbe3e4 !default;
$notice-color: #e5edf8 !default;
$info-color: $notice-color !default;
$success-color: #e6efc2 !default;

// Component settings
$badge-prefix: 'badge' !default;
$badge-font-size: 0.75em !default;
$badge-default-color: $medium-gray !default;

@mixin badge() {
  border-radius: $badge-font-size * 5;
  display: inline-block;
  font-size: $badge-font-size;
  line-height: 1;
  padding: 0.4em 1.2em;
}

@mixin badge-theme($color: $badge-default-color){
  background-color: $color;
  color: darken($color, 60%);
}

[class^="#{$badge-prefix}"]{
  @include badge;
}

[class^="#{$badge-prefix}.circle"]{
    border-radius: 500rem;
    padding: .5em;
    min-width: 2em;
    min-height: 2em;
}

.#{$badge-prefix} {
  @include badge-theme();
}

.#{$badge-prefix}-alert {
  @include badge-theme($alert-color);
}

.#{$badge-prefix}-error {
  @include badge-theme($error-color);
}

.#{$badge-prefix}-info {
  @include badge-theme($info-color);
}

.#{$badge-prefix}-success {
  @include badge-theme($success-color);
}