/* # @mixin visuallyhidden

Hide only visually, but have it available for screen readers: <h5bp.com/v>
http://webaim.org/techniques/css/invisiblecontent/

1 - preserve word spacing fo screen reader
    https://medium.com/@jessebeach/beware-smushed-off-screen-accessible-text-5952a4c2cbfe#.70jc0kefk

@mixin visuallyhidden 
@see    - <h5bp.com/v>

Styleguide libs.mixins.visuallyHidden
*/
@mixin visuallyhidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap; // [1]
    width: 1px;
}
