/* # Nav - Prev-Next

Syles for a previous/next article navigation

Markup:
<div class="nav_prev-next">
    <a class="prev" href='' title="Article précédent">
        <i class="icon-chevron-left"></i>
    </a>
    <a class="next" href='' title="Article suivant">
        <i class="icon-chevron-right"></i>
    </a>
</div>

Styleguide components.navs.nav_prev-next
*/
.nav_prev-next{
    @include clearfix;
    margin-top: .5em;
    margin-bottom: .5em;
    
    a.prev{
        float: left;
    }
    
    a.next{
        float: right;
    }
    
    & .thumbnail,
    & .content {
        display: table-cell;
        vertical-align: middle;
    }
    .prev .thumbnail{
        padding-right: $small-spacing;
    }
    .next .thumbnail {
        padding-left: $small-spacing;
    }
    .next .content {
        text-align: right;
    }
    
}