/* # Sass-require

Mixin permettant de définir des modules sass
afin qu'il ne soient exportés qu'un fois lors de la génération des css

On peut ensuite l'utiliser dans les addons pour que ils puissent êtres
étendus plusieures fois dans des modules indépendants

##Markup:

```
// Define a dep-object
@include define('sass-object') { 
  .dep-object {
    display: block;
    background-color: black;
  }
}
```

@version  - 0.0.1
@etat     - test
@mixin define(object) - define a scss object in the global $sass-require_components
@global $_sass-require__components - store all components
@see - https://github.com/mintuz/sass-require

Compatibility - untested

Styleguide core.require
*/
@if not global-variable-exists(_sass-require__components) {
  $_sass-require__components: () !global;
}

@mixin define($name) {
  @if (index($_sass-require__components, $name) == null) {
    $_sass-require__components: append($_sass-require__components, $name) !global;
    @content;
  }
}